// Black
$color-black: #000 !default;

// White
$color-white: #fff !default;
$color-white-dark: #f3f3f3 !default;
$color-white-dark-extra: darken($color-white, 6.5%) !default;
$color-white-darker: darken($color-white, 13.5%) !default;
$color-white-darker-extra: darken($color-white, 20%) !default;

// Gray
$color-gray-darker: #21262c;
$color-gray-dark: #353840;
$color-gray: lighten($color-black, 33.5%) !default;
$color-gray-light: lighten($color-black, 46.7%) !default;
$color-gray-lighter: lighten($color-black, 60%) !default;

// Alias Primary
$color-primary: #722ed1 !default;
$color-primary-sec: #722ed1 !default; //#fc6ca8
$color-primary-dark: darken($color-primary, 8%) !default;
$color-primary-light: lighten($color-primary, 8%) !default;
$color-primary-lighter: lighten($color-primary, 20%) !default;
$color-primary-low: lighten($color-primary, 33.5%) !default;

// Secondary color
$color-secondary: #71669e !default;
$color-secondary-dark: darken($color-secondary, 5%) !default;
$color-secondary-light: lighten($color-secondary, 5%) !default;

$menu-item-background: #f3f3f3 !default;
$border-color: #f2f2f2 !default;

$color-success: #52c41a !default;
$color-error: #f5222d !default;
$color-warning: #faad14 !default;
$color-progress: #308ee0 !default;

$color-text: rgba(0, 0, 0, 0.65) !default;
$color-text-secondary: rgba(0, 0, 0, 0.45) !default;
$color-text-dark: rgba(0, 0, 0, 0.85) !default;

$font-size-mini: 12px !default;
$font-size-normal: 14px !default;
$font-size-title: 16px !default;
$font-size-large: 24px !default;

$spacing-mini: 4px !default;
$spacing-small: 8px !default;
$spacing-normal: 16px !default;
$spacing-large: 24px !default;

$direction: 'top' 'right' 'bottom' 'left';
$size: 'mini' 'small' 'normal' 'large';

@for $i from 1 to 5 {
  $m: 0px;
  @if $i < 2 {
    $m: 4px;
  } @else {
    $m: ($i - 1) * 8px;
  }
  $_size: #{nth($size, $i)};
  .margin-#{$_size} {
    margin: $m;
  }
  .padding-#{$_size} {
    padding: $m;
  }
  @for $j from 1 to 5 {
    $_direction: #{nth($direction, $j)};
    .margin-#{$_direction}-#{$_size} {
      margin-#{$_direction}: $m;
    }
    .padding-#{$_direction}-#{$_size} {
      padding-#{$_direction}: $m;
    }
  }
}

.p-left-part {
  display: flex;
}

.p-right-part .ant-col-8 {
  text-align: center;
}

@media screen and (max-width: 768px) {
  .p-left-part {
    margin-bottom: 24px;
  }
}

.p-left-part {
  p {
    margin-bottom: 4px;
    font-size: 20px;
    font-weight: normal;
  }

  span.subText {
    color: $color-text-secondary;
    font-weight: normal;
  }
}

.p-right-part {
  p {
    margin: 0;
    font-size: 30px;
    font-weight: normal;
  }

  span {
    margin-bottom: 8px;
  }

  .numTitle {
    color: $color-text-secondary;
  }
}

.user-photo {
  height: 50px;
  width: 50px;
  margin-right: 16px;
  flex: 0 0 50px;
}

.platformContainer {
  padding: 24px;
}

.project-list {
  display: flex;
  flex-flow: row wrap;
  min-height: 300px;

  .projectInfo {
    flex: 0 1 31%;
    width: 31%;
    padding: 8px;
    display: inherit;
    flex-flow: inherit;
    border: 1px solid transparent;
    transition: border 0.3s;
    border-radius: 5px;
    margin: 0 1%;

    &:hover {
      border: 1px solid $color-primary-lighter;
      box-shadow: 0 2px 28px rgba($color: $color-primary-lighter, $alpha: 0.29);
    }

    .top {
      margin-bottom: 8px;

      p {
        font-weight: bold;
        margin-bottom: 8px;
        display: flex;
        align-items: center;

        &.triangle::before,
        &.rect::before {
          content: '';
          display: inline-block;
          margin-right: 8px;
          height: 20px;
          width: 20px;
          background: $color-primary-lighter;
        }

        &.triangle::before {
          border-radius: 50%;
          background: $color-primary;
        }
      }

      span {
        color: rgba(0, 0, 0, 0.45);
        display: block;
        word-break: break-all;
        height: 58px;
        overflow: hidden;
        text-overflow: ellipsis;
        /*! autoprefixer: ignore next */
        -webkit-box-orient: vertical;
        display: -webkit-box;
        -webkit-line-clamp: 3;
      }
    }

    .bottom {
      align-self: flex-end;
      display: flex;
      justify-content: space-between;
      width: 100%;
      align-items: center;
      flex-flow: row wrap;

      .time {
        color: rgba(0, 0, 0, 0.25);
        font-size: 12px;
      }

      .domain {
        font-size: 12px;
        color: rgba(0, 0, 0, 0.45);
      }
    }
  }
}

.quick-start {
  min-height: 150px;

  .ac {
    color: rgba(0, 0, 0, 0.65);
    cursor: pointer;
    transition: all 0.3s;
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: center;
    min-height: 75px;

    &:hover {
      color: $color-primary-light;

      .ant-avatar {
        transform: scale(1.2);
      }
    }

    .ant-avatar {
      margin-top: 16px;
      transition: transform 0.3s;
    }

    p {
      margin-top: 8px;
      margin-bottom: 0;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      width: 100%;
      text-align: center;
    }
  }
}

#multiAnalysis {
  height: 300px;
  width: 100%;
}

.team-card {
  min-height: 126px;
  display: flex;

  .team-card-detail {
    flex: 1;
    text-align: center;
    position: relative;

    &::before {
      content: '';
      display: inline-block;
      margin-bottom: 16px;
      height: 20px;
      width: 20px;
      border-radius: 4px;
      background: $color-primary-lighter;
    }

    span {
      color: rgba(0, 0, 0, 0.65);
      position: absolute;
      bottom: 0;
      display: block;
      text-align: center;
      width: 100%;
      left: 0;
    }
  }
}
